.App-Header {
    display: flex;
    position: fixed;
    width: 100%;

    button {
        background: orange;
        border: none;
        object-fit: cover;
        max-width: 24px;
        height: 24px;
        width: 100%;
        margin: 2px;
        padding: 2px;
        transition: all .15s ease-in-out;
        border-radius: 2px;
      
        &:focus {
          outline: none;
        }
      
        &:active {
          background-color: rgb(216, 140, 0);
        }
      
        &:hover {
          cursor: pointer;
        }

        img {
            object-fit: contain;
            max-height: 100%;
            max-width: 100%;
        }
    }

    .Menu-Btn {
        margin-left: auto;
    }
}